import React from 'react'
import {View, StyleSheet, Text} from 'react-native'
import { StackedAreaChart, YAxis, XAxis } from 'react-native-svg-charts'
import * as shape from 'd3-shape'

export default function Seagraph() {

    const data = [
        {
            month: new Date(2015, 0, 1),
            apples: 3840,
            bananas: 1920,
        },
        {
            month: new Date(2015, 1, 1),
            apples: 1600,
            bananas: 1440,
        },
        {
            month: new Date(2015, 2, 1),
            apples: 640,
            bananas: 960,
        },
        {
            month: new Date(2015, 3, 1),
            apples: 3320,
            bananas: 480,
        },
    ]

    const datayaxis = [0, 20, 40,60]
    const contentInset = { top: 20, bottom: 20 }

    const colors = ['#4bce98', '#d6d6d6']
    const keys = ['apples', 'bananas']

    const svgs = [
        { onPress: () => console.log('apples') },
        { onPress: () => console.log('bananas') },
    ]

    return (
        <View style={styles.container}>
            <Text style={styles.text}>
                Lorem Ipsun 
            </Text>
            <View style={styles.graphcontainer}>
                <View style={styles.yaziscontainer}>
                    <YAxis
                        data={datayaxis}
                        contentInset={contentInset}
                        svg={{
                            fill: 'grey',
                            fontSize: 15,
                        }}
                        numberOfTicks={4}
                        formatLabel={(value) => value}
                    />
                </View>
                <View style={styles.seacontainer}>
                    <StackedAreaChart
                        style={{ height: '100%' }}
                        data={data}
                        keys={keys}
                        colors={colors}
                        curve={shape.curveNatural}
                        showGrid={false}
                        svgs={svgs}
                    />
                    <XAxis
                        style={{ marginHorizontal: -10, marginVertical: 4 }}
                        data={data}
                        formatLabel={(value, index) => value}
                        contentInset={{ left: 10, right: 10 }}
                        svg={{ fontSize: 10, fill: 'black' }}
                    />
                </View>
            </View>
        </View>
    )
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
    },
    graphcontainer: {
        flexDirection: 'row',
        flex: 1,
        marginTop: 3
    },
    yaziscontainer: {
        flex: .03,
        flexDirection: 'row'
    },
    seacontainer : {
        flex: .97
    },
    text: {
        color: 'gray',
        fontSize: 22,
    }
})
