const changevalue = (value) =>{
    switch(value){
        case 0 :
            return 'Ene'
        break;
        case 1 :
            return 'Feb'
        break;
        case 2 :
            return 'Marz'
        break;
        case 3: 
            return 'Abr'
        break;
        case 4: 
            return 'May'
        break;
        case 5:
            return 'Jun'
        break;
        case 6 :
            return 'Jul'
        break;
        case 7 :
            return 'Ago'
        break;
        case 8 :
            return 'Sep'
        break;
    }
}

export default {changevalue}