const color = (index) =>{
    switch(index){
        case 0:
            return '#71cff1'
        break;
        case 1:
            return '#95e3ff'
        break;
        case 2 :
            return '#43b1d7'
        break;
        case 3: 
            return '#59c1e5'
        break
    }
}

export default {color}